	module.exports = {"data":{"markdownRemark":{"html":"<div>\n          <div\n            class=\"gatsby-resp-iframe-wrapper\"\n            style=\"padding-bottom: 56.25%; position: relative; height: 0; overflow: hidden;\"\n          >\n            <iframe src=\"https://player.vimeo.com/video/238242365\" frameborder=\"0\" webkitallowfullscreen=\"\" mozallowfullscreen=\"\" allowfullscreen style=\"\n            position: absolute;\n            top: 0;\n            left: 0;\n            width: 100%;\n            height: 100%;\n          \"></iframe>\n          </div>\n          </div>\n<h1>Brief</h1>\n<p><em>Endless Horizons: Images for Machine Learning &#x26; Leisure</em> is a video installation examining computer vision.</p>\n<h1>Motivation</h1>\n<p>With the rapid flow and continuous accumulation of digital images in the cloud, it is becoming apparent that these pictures are now made more for machine, rather than human consumption. While scraping panoramic images of the US coast from the NDBC’s network of camera-enabled buoys, we wondered how a computer might interpret or “see” these many beautiful, and at times tumultuous, vistas of virtually endless sea and sky. Using computer vision, each panorama has been computationally “reduced” to a series of simple color gradients and lines. The panoramic photos are only taken once per hour, so the video piece fills in the gaps of the undocumented passage of time. The questions here touch on the fine line between the mundane and the sublime, and on what may be lost and emphasized when machines take on these now endless tasks of seeing.</p>\n<h1>Sources</h1>\n<ul>\n<li><a href=\"http://www.ndbc.noaa.gov/buoycams.shtml\">The National Buoy Data Center</a></li>\n<li><a href=\"https://github.com/mikewesthad/buoy-analyzer\">Code repository</a></li>\n</ul>","frontmatter":{"date":"October 15, 2017","title":"Endless Horizons","subtitle":"Single-channel video installation, 2017"}}},"pathContext":{}}

